@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;500&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: 'Poppins',-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #eee !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#btn {
    background-color: #de5246;
    color: white;
    text-align: center;
    padding: 7px 5px;
    border-radius: 5px;
    cursor: pointer;
}
a{
    text-decoration: none;
    color: inherit;
}
#navbar {
    display: flex;
    padding: 20px;
    align-items: center;
    background-color: white;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    border-bottom: 1px solid #dde0e1;
    justify-content: space-between;
    position: sticky;
    top: 0;
    left: 0;
}
nav h3 {
    text-transform: uppercase;
	background: linear-gradient(to right, #30CFD0 0%, #330867 100%);
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
    font-size: 25px;
}
#navbar img {
    width: 45px;
    height: 45px;
    border-radius: 20px;
}
.nav-user {
    display: flex;
    align-items: center;
    position: relative;
}
.nav-user .drop-down {
    width: 45px;
    height: 45px;
    border-radius: 50%;
    background-color: #9e9e9e;
    justify-content: center;
    align-items: center;
    display: flex;
    color: white;
    font-size: 20px;
    margin-left: 10px;
    cursor: pointer;
    position: relative;
}
.drop-down-menu {
    position: absolute;
    background-color: #9e9e9e;
    width: 225px;
    /* height: 200px; */
    top: 55px;
    right: -11px;
    border-radius: 7px;
    opacity: 0;
    padding: 15px;
    transition: 0.2s opacity;
}
.drop-down-menu.active {
    opacity: 1;
    transition: 0.5s;
}
.drop-down-menu .info {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    padding-bottom: 10px;
}
.drop-down-menu p {
    padding: 5px 0;
    cursor: pointer;
}


.header{
    display: grid;
    grid-template-columns: 1fr 10fr;
    align-items: center;
    grid-gap: 30px;
    gap: 30px;
}
.skeleton {
    background-color: #ddd;
    margin: 10px 0;
    border-radius: 4px;
    overflow: hidden;
    position: relative;
}
.skeleton::after {
    content: "";
    -webkit-animation: loading 1.1s infinite;
            animation: loading 1.1s infinite;
    position: absolute;
    width: 100%;
    height: 100%;
    
    background-color: rgba(255,255,255,0.2);
}
.skeleton.title {
    width: 30%;
    height: 20px;
}
.skeleton.text {
    width: 100%;    
    height: 12px;
}
.skeleton.avatar {
    width: 90px;
    height: 90px;
    border-radius: 50%;
}
.skeleton.image {
    width: 100%;
    height: 500px;
}
@-webkit-keyframes loading {
    0% {
        -webkit-transform: translateX(-150%);
                transform: translateX(-150%);
    }
    100% {
        -webkit-transform: translateX(150%);
                transform: translateX(150%);
    }
}
@keyframes loading {
    0% {
        -webkit-transform: translateX(-150%);
                transform: translateX(-150%);
    }
    100% {
        -webkit-transform: translateX(150%);
                transform: translateX(150%);
    }
}
.posts {
    width: 65%;
    margin: 0 auto;

}
.post {
    margin-bottom: 40px;
    background-color: white;
    padding: 20px;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}
.post .post-header {
    display: flex;
    align-items: center;
}
.post .post-header img {
    width: 40px;
    height: 40px;
    justify-self: flex-end;
    border-radius: 50%;
}
.post .post-header .username {
    margin-left: 10px;
    width: 90%;
    text-align: start;
    font-size: 17px;
}
.post-header .delete-post {
    border: none;
    font-family: "Poppins", sans-serif;
    background-color: #fff;
    font-size: 15px;
    cursor: pointer;
    transition: 0.3s;
}
.post-header .delete-post:hover {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
}
.post-image img {
    width: 100%;
    margin: 20px 0 2px;
}
.post-caption {
    text-align: start;
    padding-left: 5px;
}
.post-caption span {
    font-weight: 700;
    width: 100%;
}
.post-caption p {
    font-weight: 200;
    padding-left: 7px;
    display: inline;
}
.post-comments {
    text-align: start;
    padding-left: 5px;
    display: flex;
}
.post-comments .username-comment {
    font-weight: 700;
}
.post-comments .comment {
    padding-left: 4px;
    font-weight: 200;
}
.add-comment {
    display: flex;
    align-items: center;
    width: 100%;
    position: relative;
}
.add-comment textarea {
    resize: none;
    width: 100%;
    position: relative;
    border: none;
    outline: none;
    padding: 20px 0;
    font-family: "Poppins", sans-serif;
    font-size: 15px;
}
.add-comment button {
    border: none;
    height: 40px;
    background-color: #fff;
    font-family: "Poppins", sans-serif;
    font-size: 15px;
    cursor: pointer;
    transition: 0.3s;
}
.add-comment button:hover {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
}

@media(max-width:765px){
    .posts{
        width: 90%;
    }
}
 .create-post {
    width: 65%;
    margin: 0 auto;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    border-bottom: 1px solid #dde0e1;
    margin-bottom: 30px;
}
 .create-post p {
    text-align: start;
    padding: 15px;
}
 .create-post textarea {
    border: none;
    outline: none;
    height: 80px;
    padding: 10px;
    resize: none;
    font-size: 16px;
    font-family: "Poppins", sans-serif;
}
.create-post .actions {
    display: flex;
    justify-content: space-between;
    padding: 15px;
}
 .create-post .actions button {
    border: none;
    font-family: "Poppins", sans-serif;
    background-color: white;
    font-size: 16px;
    cursor: pointer;
    transition: 0.2s;
}
 .create-post .actions button:hover,
 .create-post .actions label:hover
{
    transform:scale(0.95);
    -webkit-transform: scale(0.95);
}
 .create-post .actions label {
    font-size: 20px;
    transition: 0.2s;
}
#Image-Preview {
    display: none;
}
.create-post-user-image {
    width: 50px;
    height: 50px;
    border-radius: 50%;
}
@media(max-width:765px){
    . .create-post{
        width: 90%;
    }
}

#override-bootstrap .page {
    min-height: 100vh;
    background-color: #eee;
}
#override-bootstrap .sign-in {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px 0;
}


.profile {
    padding: 100px 0;
    width: 65%;
    margin: 0 auto;
    text-align: center;
}
.name {
    display: flex;
    align-items: center;
    justify-content: center;
}
.verification {
    width: 20px;
    height: 20px;
    margin-left: 5px;
}
.profile-img {
    border-radius: 50%;
}
.num-of-posts {
    margin-top: 10px;
}
.counter {
    background-color: #8bc34a;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: 0 auto;
    padding: 2px 10px;
    border-radius: 10px;
    color: white;
    display: inline;
}
